
.data-table{
  color: rgb(66, 185, 131);
  width: 100%;
  margin-top: 16px;
  margin-bottom: 16px;
}

td, th{
  text-align: left;
  padding: 16px;
}

th{
  border-bottom: 1px solid rgba(66, 185, 131,0.2);
}

tr:not(:last-child) {
  border-bottom: 1px solid rgba(66, 185, 131,0.2);
}

td:not(:last-child){
  border-right: 1px solid rgba(66, 185, 131,0.2);
}

td.thin{
  width: 1%;
  white-space: nowrap;
}

tr:nth-child(even){
  background-color: rgba(66, 185, 131,0.1);
}




